/*  ===== MOBILE styles (reside at the top and are inherited by all devices) ===== */
/*  ===== See tablet, desktop, and print @media queries at the bottom. */

/*   ----- Imports  -----  */
/* Add fonts from the app's server
    @font-face {
      font-family: 'FontName';
      src: url('FontName.woff2') format('woff2');
    }
    ...or hosted services like Google Fonts
    @import url(//fonts.googleapis.com/css?family=Font+Name);
  */
@import url("https://fonts.googleapis.com/css2?family=Caveat:wght@400;700&family=Chivo+Mono:ital,wght@0,200;0,400;0,600;1,200;1,400;1,600&family=Quicksand:wght@300;400;500;600;700&display=swap");
/* @import "node_modules/modern-normalize/modern-normalize.css"; */
/* npm install modern-normalize
          Resets elements for browser consistency.
          https://www.npmjs.com/package/modern-normalize

  /* Component-specific stylesheets, if any.
    @import "component1.css";
  */
@import "~react-image-gallery/styles/css/image-gallery.css";

/*   ----- Variables  -----  */
:root {
  --dark-blue: #121c31;
  --medium-blue: #4681a6;
  --light-blue: #a1f2f0;
  --contrast-color: hsl(359, 48%, 71%);
  --color-with-transparency: hsla(213, 80%, 82%, 0.376);
  --navigation-color: #dacb84;
  --figure-background-color: #d2e9f1;
  --dark-red: #cc0303;
}

/*  ----- Viewport ----- elements inherited by all remaining elements ----- */

body {
  /* Can alternatively define in html {} or :root {} */
  background: url("images/background.jpeg") no-repeat fixed center;
  background-size: cover;
  max-width: 100%;
  font-family: "Quicksand", sans-serif; /* Define with at least one font name and its related generic family name.*/
  font-size: 1em;
  color: var(--dark-blue);
  margin: 0 auto;
  padding: 3%;
}

/*  ----- Page layout blocks -----  */
header {
  font-size: 18px;
  padding-right: 2%;
  padding-left:  1%;
  margin-top: 10px;
}
main {
  background-color: var(--color-with-transparency);
  border-radius: 5px;
  margin: 10px 10px 0 10px;
  padding: 2% 5% 3% 5%;
}
section {
}
article {
  background-color: var(--color-with-transparency);
  border-radius: 10px;
  border-top-style: double;
  padding: 3%;
  margin-top: 1%;
}
#topic1 {
}
#topic2 {
}

div {
}
aside {
}
footer {
  background-color: var(--medium-blue);
  color: var(--color-with-transparency);
  padding: 0;
  margin-left: 10px;
  margin-right: 10px;
}

/* ----- Anchors ----- Text links */
a {
  display: inline-flex;
}
a:link {
}
a:hover {
}
a:active {
}
a:visited {
}

/*  ----- Navigation ----- Override text links for navigation only.  */
nav {
  margin: 10px;
}

nav a {
  background-color: var(--navigation-color);
  color: var(--dark-blue);
  border-radius: 3px 3px 3px 3px;
  font-size: 1.1em;
  font-weight: bold;
  padding: 3px 20px 3px 20px;
  text-decoration: none;
}

nav a:hover {
  color: var(--light-blue);
}

.same-page-navigation {
  display: flex;
  padding: 0;
  margin: 0 auto;
}

.same-page-navigation a{
  background-color: var(--light-blue);
  font-size: 0.9em;
  padding: 3px 15px 3px 15px;
  margin: auto;
}

.same-page-navigation a:hover {
  color: var(--medium-blue);
}
