/*  ----- Table ----- */
table {
  background-color: var(--color-with-transparency);
  padding: 3%;
  border: 1px solid var(--navigation-color);
  width: 99%;
}
caption {
  color: var(--dark-blue);
  text-align: left;
  padding-left: 3%;
  background-color: var(--navigation-color);
  margin-right: 30%;
}
thead {
}
tbody {
}
tfoot {
}
tr {
  text-align: center;
  color: var(--dark-blue);
}
tr:nth-child(even) {
  background-color: var(--color-with-transparency);
}
tr:nth-child(odd) {
  background-color: var(--figure-background-color);
}
th {
  background-color: var(--medium-blue);
  color: var(--dark-blue);
}
td {
  padding: 6px;
}
td:nth-child(1) {
}
.rightAlign {
  text-align:right;
}
colgroup {
}
data {
}
datalist {
}

/*  ----- Form ----- */
form {
  background-color: var(--figure-background-color);
  border-radius: 5px;
}
fieldset {
  color: var(--dark-blue);
  font-weight: normal;
  margin-top: 5px;
  padding: 2%;
}
legend {
  border-top: 1px solid;
  border-left: 1px solid;
  border-right: 1px solid;
  border-radius: 5px 5px 0 0;
  padding: 0 8px 3px 8px;
  font-size: 15px;
}
label {
  color: var(--dark-blue);
  font-weight: normal;
}
button {
  background-color: var(--color-with-transparency);
  line-height: 20px;
  width: 80px;
  border-radius: 10px;
  color: var(--medium-blue);
  letter-spacing: 1px;
  margin-top: 10px;
  margin-bottom: 10px;
  margin-left: 1%;
}
input {
  color: var(--dark-blue);
  margin: 1%;
}
input:focus {
  background-color: var(--contrast-color);
}
output {
}
meter {
}
progress {
}
select {
  font-size: 14px;
  padding: 3px;
}
optgroup {
}
option {
}
textarea {
  width:250px;
  height:100px;
}
*::placeholder{
  color: var(--figure-background-color);
}

*:required:invalid {
  border: 1px solid var(--dark-red);
}

label.required:after {
  content: " *";
  color: var(--dark-red);
  font-weight: bold;
}

